import { useState } from "react";
import { useFinanceTransactionHistoryRemoveCard } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../Income/Income.module.css";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import CreateButton from "../../../Ui/Button/CreateButton";

const FinanceReceiptCardMenu = ({ onClose, frid, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [state, setState] = useState({
    query: "",
  });
  const [isDelete, setIsDelete] = useState(false);

  const [financeTransactionHistoryRemoveCard] =
    useFinanceTransactionHistoryRemoveCard();

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onDelete = () => {
    let validation = requiredField(state);
    if (existingRequiredField(validation)) {
      if (state.query === "Confirm" && frid) {
        setIsDelete(false);
        setDisabled((pre) => !pre);
        financeTransactionHistoryRemoveCard({
          frid: frid,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      {isDelete ? (
        <Modal onClose={() => setIsDelete(false)}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("are_your_sure_to_delete_fee_receipt")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={() => setIsDelete(false)}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("type_confirm_to_continue")}
                placeholder={t("type_confirm_to_continue")}
                name="query"
                value={state.query}
                type="text"
                onChange={onInputChange}
                errorShow={errorField["query"]}
              />
              <CreateButton label="confirm" onAction={onDelete} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : (
        <ModalMenu
          onClose={onClose}
          disabled={disabled}
          customStyle={{
            width: "18rem",
          }}
        >
          <h6 onClick={() => setIsDelete(true)}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default FinanceReceiptCardMenu;
